<template>
  <div class="wrp">
    <div class="nav-bar">
      <text class="iconfont" @click="onBack">&#xe8b5;</text>
      <text class="title"></text>
      <text class="title"></text>
    </div>
    <div class="form-item">
      <text></text>
      <input v-model="name" type="text" placeholder="" class="input"/>
    </div>
    <div class="form-item">
      <text></text>
      <textarea v-model="desc" type="text" placeholder="" class="textarea"/>
    </div>
    <div class="b-btn-wrp">
      <text class="b-btn" @click="onAdd"></text>
    </div>
  </div>
</template>

<script>
const storage = weex.requireModule('storage')
const navigator = weex.requireModule('navigator')
export default {
  name: 'add',
  data () {
    return {
      name: '',
      desc: ''
    }
  },
  beforeCreate () {
    const domModule = weex.requireModule('dom')
    domModule.addRule('fontFace', {
      'fontFamily': "iconfont",
      'src': "url('http://at.alicdn.com/t/font_571013_v2d0ejzy8rtz9f6r.ttf')"
    })
  },
  methods: {
    onAdd () {
      let todoEvents
      storage.getItem('todoEvents', (e) => {
        if (e.result === 'sucess') {
          todoEvents = JSON.parse(e.data)
        } else {
          todoEvents = []
        }
        todoEvents.push({
          name: this.name,
          desc: this.desc
        })
        storage.setItem('todoEvents', JSON.stringify(todoEvents), (e) => {
          if (e.result === 'success') {
            navigator.pop({
              animated: 'true'
            })
          }
        })
      })
    },
    onBack () {
      navigator.pop({
        animated: 'true'
      })
    }
  }
}
</script>

<style scoped>
.iconfont {
  font-family: iconfont;
}
.wrp {
  flex-direction: column;
  background-color: #F0F0F2;
}
.nav-bar {
  height: 88px;
  background-color: #fff;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
}
.form-item {
  flex-direction: row;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 40px;
}
.input {
  flex: 1;
}
.textarea {
  flex: 1;
}
.b-btn-wrp {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 750px;
  height: 100px;
  background-color: #FFF;
  flex-direction: row;
  align-items: center;
}
.b-btn {
  flex: 1;
  text-align: center;
}
</style>





